.footer {
  height: 202px;
  //padding: 31px 397px;
  justify-content: space-around;
  width: 32%;
  margin-left: 5%;
  margin-bottom: 20px;
  flex-direction: column;
  align-items: flex-start;


  @include desktop{
    flex-direction: row;
    margin-left: 34%;
    height: 84px;
    align-items: center;

  }

  NavLink, a {
    text-decoration: none;
    color: inherit;
  }


  .footer_link {
    margin: 0;
    font-size: 18px;
    font-weight: $regular-weight;
    letter-spacing: $letter-spacing-3;

    &:hover {
      cursor: pointer;
      animation-name: button_hover;
      animation-duration: 0.3s;
    }
  }
}
